.right {
    margin-top: 250px;
    margin-left: 50px;
    margin-right: 50px;
    width: 250px;
    height: 210px;
    background-color: blue;
    border-radius: 10px;
    text-align: center;
    box-shadow: 2px 2px 5px white inset, 5px 5px 10px black;
    padding: 30px;
}

.tab {
    display: inline-block;
    height: 150px;
    border: 2px solid gold;
    border-radius: 10px;
    width: 230px;
    padding: 10px;
}

.default {

    display: inline-block;
    padding: 15px 30px;
    font-size: 16px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #c9b117;
    border: none;
    border-radius: 15px;
    box-shadow: 0 5px #c73434;
    margin: 7px;

}

.default:hover {
    background-color: rgba(54, 140, 243, 0.81)
}

.default:active {
    background-color: #c9b117;
    box-shadow: 0 5px #c73434;
    transform: translateY(3px);
}

.default:disabled {
    background-color: lightslategrey;
}

.number {
    margin-top: 15px;
    font-style: italic;
    font-size: 50px;
    font-weight: bold;
}

.red {
    color: red;
    margin-top: 15px;
    font-style: italic;
    font-size: 50px;
    font-weight: bold;
}

.error {
    border: 3px solid red;
    color: red;
    font-style: italic;
    font-size: 25px;
    font-weight: bold;
}
